.nav{
  display: flex;
  color: red;
  padding: 5px 10px 5px 40px;
  
  list-style: none;
  background-color: gray;
  border-radius: 12px;

}

.nav li{
  margin: 20px;
  
  border-radius: 12px;
  font-size: 30px;

}

#textform{

margin-left: 100px;
display: block;
}


.btn{
  text-align: center;
  justify-content: center;
  padding: 5px 10px;
  border-radius: 12px;
  margin: 10px;
  margin-left: 100px;
  display: flex;
  background-color: rgb(187, 31, 31);
  color: aliceblue;
  border: 2px solid rgb(0, 0, 0);
  display: inline;

}
.btn :hover{
  background-color: red;
  color: aqua;
  border: 2px solid blue;
  cursor: pointer;
}

